.buttons {
   display: flex;
   flex-direction: row;
   align-items: center;
   flex-wrap: wrap;
   width: 100%;
   margin: 15px 2.5% 45px;

   @media (max-width: 951px) {
      margin: auto 2.5% 45px;
   }

   @media (max-width: 851px) {
      margin-left: 5px;
   }
}

.submit-btn,
.reset-btn {
   min-width: 76px;
   padding: 6px 16px;
   font-size: 0.875rem;
   line-height: 1.75;
   letter-spacing: 1px;
   text-transform: uppercase;
   border-radius: 4px;
   border-style: none;
   color: #fff;
   background-color: #596e07;
   box-shadow: 0px 3px 1px -2px #0003, 0px 2px 2px 0px #00000024,
      0px 1px 5px 0px #0000001f;
   cursor: pointer;

   &:hover {
      transition: background-color 200ms cubic-bezier(0.65, 0.05, 0.36, 1);
      color: #fff;
      background-color: #435500;
   }

   &:active {
      transition: background-color 200ms cubic-bezier(0.65, 0.05, 0.36, 1);
      background-color: #344201;
      box-shadow: 0px 2px 4px -1px #0003, 0px 4px 5px 0px #00000024,
         0px 1px 10px 0px #0000001f;
   }
}

.reset-btn {
   border: 1px solid #596e07;
   color: #596e07;
   background-color: transparent;

   &:active {
      border: 1px solid #596e07;
      color: #fff;
      background-color: #344201;
   }
}
